body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-inter: Inter;

  /* font sizes */
  --font-size-xs: 12px;
  --font-size-sm: 14px;
  --font-size-smi: 13px;

  /* Colors */
  --color-whitesmoke-100: #f8f9fa;
  --color-whitesmoke-200: #ebf0f3;
  --color-gainsboro-100: rgba(217, 217, 217, 0.5);
  --primary-color: #07c1cf;
  --color-darkslategray: #3f3f3f;
  --color-gray-100: #fefefe;
  --color-gray-200: #fdfdfd;
  --color-white: #fff;
  --color-aliceblue: #e3eaef;
  --icon-color: #6c757d;

  /* Gaps */
  --gap-sm: 14px;
  --gap-mid: 17px;

  /* Paddings */
  --padding-base: 16px;
  --padding-lg: 18px;
  --padding-sm: 14px;
  --padding-11xl: 30px;
  --padding-7xs: 6px;

  /* border radiuses */
  --br-7xl: 26px;
  --br-8xs: 5px;
  --br-6xs-7: 6.7px;
  --br-9xs-4: 3.4px;
  --br-31xl: 50px;
}
